html {
   background-color: #f7f7f7; 
}
.App {
  text-align: center;
  width:100%;
  height: 1700px;
  padding: 0;
  margin: 0;
  background-color: #f7f7f7;
  color: #000000;
  font-size: 12px;
  position: relative;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.app-nav {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
  margin-top: 0px;
  z-index: 50;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.sub-title {
  font-size: 22px;
  font-weight: 800;
  padding-top: 32px;
}

.app-nav ul {
  display: flex;
  flex-direction: row;
  width: 380px;
  justify-content: space-between;
}

.app-nav li {
  list-style: none;
  text-decoration: none;
}

.app-nav li a {
  color: #000000;
  text-decoration: none;
  cursor: pointer;
}

.home-hover a:hover {
  color: white;
}

.icons-container {
  display: flex;
  width: 120px;
  justify-content: space-around;
  margin-right: 12px;
}

.icons-container a{
  text-decoration: none;
    width: 20px;
  height: 20px;
  display: inline-block
}

.icons-container .email-icon {
  background-image: url("/email-logo.svg");
}

.icons-container .behance-icon {
  background-image: url("/behance-logo.svg");
}

.icons-container .linkedin-icon {
  background-image: url("/linkedin-logo.svg");
}

.icons-container .email-icon:hover {
  background-image: url("/email-white.svg");
}

.icons-container .behance-icon:hover {
  background-image: url("/Behance-white.svg");
}

.icons-container .linkedin-icon:hover {
  background-image: url("/Linkedin-white.svg");
}

.footer-container {
  position: absolute;
  bottom: 0;
  height: 260px;
  width: 100%;
  text-align: center;
  z-index: 9;
}

.footer-container .footer-title {
  font-size: 210px;
  font-weight: 800;
  line-height: 190px;
}

.projects-container {
   display: flex;
   justify-content: space-around;
   align-items: center;
   width: 100%;
   height: 680px;
   margin-top: 60px;

}

.project-preview-item {
  width: 400px;
  height: 550px;
  display: inline-block;
}

.collage-preview-item {
  width: 400px;
  height: 550px;
  display: inline-block;
}


.projects-container .collage-preview-item:hover {
  cursor: pointer;
}

.preview-summary {
  width: 45%;
  text-align: left;
  margin: 0 auto;
  margin-top: 320px;
  line-height: 8px;
}

.author-name {
  float: right;
}

.about-container {
  display: flex;
  justify-content: center;
  width: 900px;
  margin: 0 auto;
  padding-left: 150px;
}

.about-container .avatar{
  width: 200px;
  height: 380px;
  flex: 1;
}

.self-intro {
  text-align: left;
  line-height: 8px;
  margin-left: 24px;
  flex: 4;
}